const floatAnimation = keyframes`
  0% { transform: translateY(0); }
  50% { transform: translateY(35px); }
  100% { transform: translateY(0); }
`;

const AnimatedHtmlContainer = styled.div`
  font-size: 90px;
  color: white;
  width: 540px;
  height: 240px;
  background-image: url(${(props) => props.bg});
  background-size: cover;
  text-align: center;
  cursor: normal;

  animation: ${floatAnimation} 3s infinite ease-in-out;
  @media screen and (max-width: 768px) {
    width: 300px;
    height: 150px;
    font-size: 6vw;
  }

`;

/* ----------------  */
const MacBook = () => {
  const lidRef = useRef(); //   
  const [isOpen, setIsOpen] = useState(false); // / 
  const [angle, setAngle] = useState(0); //   
  const targetAngle = isOpen ? Math.PI / 1.5 : 0; //  
  const speed = 0.1; //  

  const MacBookScreen = () => {
    const { scene } = useGLTF(process.env.PUBLIC_URL + '/appletop.glb'); //   
    return (
      <>
        <primitive object={scene} scale={[0.5, 0.5, 0.5]} />
      </>
    )
  };
  const MacBookBottom = () => {
    const { scene } = useGLTF(process.env.PUBLIC_URL + '/btm.glb'); //   
    return <primitive object={scene} scale={[0.5, 0.5, 0.5]} />;
  };



  useFrame(() => {
    if (lidRef.current && Math.abs(angle - targetAngle) > 0.01) {
      const newAngle = angle + (isOpen ? speed : -speed);
      setAngle(newAngle);
      lidRef.current.rotation.x = -newAngle; //  
    }
  });

  const handleClick = () => {
    setIsOpen(!isOpen);
  };

  return (
    <group onClick={handleClick}>
      {/*   */}

      {/* <mesh position={[0, -1.1, 0]} receiveShadow>
        <boxGeometry args={[3, 0.1, 2]} />
        <meshStandardMaterial color="gray" />
      </mesh> */}
      <group position={[0, -0.903,0]} castShadow>  
      <MacBookBottom/>
      <BackgroundImage style={{ pointerEvents: "none" }} />
  
      </group>

      {/*   */}
      <group ref={lidRef} position={[0, -0.85,-1]} castShadow>
     {/*  <mesh ref={lidRef} position={[0, 0.05, 1]} castShadow>
        <boxGeometry args={[3, 0.05, 2]} />
        <meshStandardMaterial color="royalblue">
          <meshStandardMaterial map={screenTexture} />
        </meshStandardMaterial>
      </mesh> */}
      <MacBookScreen/>

      {/* ---------- open  */}
      {!isOpen && (
      <Html
        position={[0, 0.7, 0.5]} //   ( )
        transform
        distanceFactor={1.5} //  
        style={{
          cursor: 'normal',
        }}
      >
         <AnimatedHtmlContainer bg={Clickimg}>
          <div></div>
         </AnimatedHtmlContainer>
      </Html>
    )}

             {/* iframe */}
             {isOpen && ( //    iframe 
          <Html
            position={[0, 0, 1]} // iframe 
            rotation={[-29.84, 0, 0]} // iframe  ( )
            transform
            distanceFactor={1.5}
            style={{
              width: '780px',
              height: '510px',
              border: '2px solid rgba(0,0,0,0.5)',
              borderRadius: '15px',
              overflow: 'hidden',
              opacity:'1',
            }}
          >
            <iframe
              src="https://gumi-gomi.github.io/kmong_pj/build/" //   URL
              title="MacBook Screen"
              width="100%"
              height="100%"
              style={{ border: 'none' }}
            />
          </Html>
        )}

      </group>
    </group>
  );
};

