// ----------------- 

const Stars = () => {
  const pointsRef = useRef();
  const particleCount = 200; //  
  const positions = new Float32Array(particleCount * 3); // x, y, z   

  //     
  useEffect(() => {
    for (let i = 0; i < particleCount; i++) {
      positions[i * 3] = (Math.random() - 0.5) * 20; // x 
      positions[i * 3 + 1] = (Math.random() - 0.5) * 10; // y 
      positions[i * 3 + 2] = (Math.random() - 0.5) * 50; // z  ()
    }
    if (pointsRef.current) {
      pointsRef.current.geometry.setAttribute(
        'position',
        new THREE.BufferAttribute(positions, 3)
      );
    }
  }, [positions]);

  // z  
  useFrame(() => {
    for (let i = 0; i < particleCount; i++) {
      positions[i * 3 + 2] += 0.02; // z 
      if (positions[i * 3 + 2] > 25) {
        positions[i * 3 + 2] = -25; //   
      }
    }
    if (pointsRef.current) {
      pointsRef.current.geometry.attributes.position.needsUpdate = true; //  
    }
  });

  return (
    <points ref={pointsRef}>
      <bufferGeometry />
      <pointsMaterial size={0.04} color="white" />
    </points>
  );
};

// -----------------  
const useWindowSize = () => {
  const [size, setSize] = useState([window.innerWidth, window.innerHeight]);

  useEffect(() => {
    const handleResize = () => setSize([window.innerWidth, window.innerHeight]);

    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return size; // [width, height]
};


function App() {

  const [width, height] = useWindowSize();

  //    fov  position   
  const calculateFov = () => Math.max(16, (height / width) * 20); //  16,    
  const calculatePosition = () => {
    if (width < 768) {
      return [-3, 1.5, 8]; //  
    } else if (width < 1024) {
      return [-3, 2, 9]; //  
    } else {
      return [-3, 2, 10]; //  
    }
  };

  const cameraFov = calculateFov();
  const cameraPosition = calculatePosition();
  
  return (
   <>
<Canvas 
/*   gl={{ 
    antialias: true,  
    powerPreference: "high-performance" 
  }} */
    shadows 
    style={{ width: "100vw", height: "100vh", zIndex:'10' }}
    // camera={{position: [-3,2,10], fov:16}}
    camera={{
      position: cameraPosition,
      fov: cameraFov,
    }}
    >
  

      <OrbitControls
          minPolarAngle={Math.PI / 3}
          maxPolarAngle={Math.PI / 2}
          minAzimuthAngle={-Math.PI / 6}
          maxAzimuthAngle={Math.PI / 6}
          enablePan={false}
          // enableZoom={false}
          minDistance={11} //   
          maxDistance={13} //   
        />
        <ambientLight intensity={0.5} />
        <directionalLight position={[5, 3, 1]} intensity={0.7} castShadow />
        <MacBook />
    </Canvas>

     <Canvas 
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100vw',
          height: '100vh',
          zIndex: -1,
          background: 'black',
        }}
     >
      <ambientLight intensity={0.5} />
      <BackgroundText/>
      <Stars />
    </Canvas>
     
   </>
  );
}

export default App;

